
#Keypad{
  background : rgb(170, 15, 15);
  flex-basis: 70%;
  display: grid;
  grid-template-columns : repeat(auto-fit,minmax(25%, 1fr));
  grid-auto-rows: auto;
  grid-auto-flow: dense;
}

.col-2{
  grid-column: span 2;
}

.row-2{
  grid-row : span 2;
}

#Keypad button{
  background :rgb(80, 14, 14);
  color : white;
  border: 1px solid black;
  font-size: 20px;
}

button:active{
  transform: scale(0.9);
}